.navbar {
     padding:20px 0px;
     display:flex;
     justify-content:space-evenly ;
     align-items: center;
}
.app{
    width: 80%;
    margin: auto;
}
.navbar .logo{
    width: 150px;
}

.navbar-menu{
    display: flex;
    list-style: none;
    gap:20px;
    color:#49557e;
    font-size:18px;
    cursor: pointer;
}

.navbar-right{
  display: flex;
  align-items: center;
  gap:40px;
}

.navbar button {
    background: transparent;
    font-size: 16px;
    color:#49557e;
    border:1px solid tomato;
    padding:10px 30px;
    border-radius: 50px;
    cursor:pointer;
    transition: 0.3s;
}

.navbar button:hover{
    background-color: #fff4f2;
}

.navbar .active{
    padding-bottom: 2px;
    border-bottom: 2px solid #49557e;
}

.navbar link {
    cursor: pointer;
}

.navbar-search-icon {
  position: relative;
}

.navbar-search-icon .dot {
  position: absolute;
  width: 10px;
  height: 10px;
  background-color: tomato;
  border-radius: 50%;
  top: -5px;
  right: -5px;
}


@media (max-width: 1050px) {
    .navbar .logo{
        width: 140px;
    }
    .navbar-menu{
        gap: 10px;
        font-size: 17px;
    }
    .navbar-right{
        gap: 30px;
    }
    .navbar-right img{
        width: 22px;
    }
    .navbar-right button {
        padding:8px 25px;
    }
}

@media (max-width: 900px) {
    .navbar .logo{
        width: 120px;
    }
    .navbar-menu{
        gap: 15px;
        font-size: 16px;
    }
    .navbar-right{
        gap: 20px;
    }
    .navbar-right img{
        width: 20px;
    }
    .navbar-right button {
        padding:7px 20px;
        font-size: 15px;
    }
}

@media (max-width: 750px) {

    .navbar-menu{
       display: none;
    }
  
}